import { setCookie, deleteCookie } from "../../utils/cookies";
import { TUserActions } from "../actions/auth";
import {
  REGISTRATION_REQUEST,
  REGISTRATION_SUCCESS,
  REGISTRATION_ERROR,
  AUTHORIZATION_REQUEST,
  AUTHORIZATION_SUCCESS,
  AUTHORIZATION_ERROR,
  PASSWORD_RECOVERY_REQUEST,
  PASSWORD_RECOVERY_SUCCESS,
  PASSWORD_RECOVERY_ERROR,
  PASSWORD_UPDATING_REQUEST,
  PASSWORD_UPDATING_SUCCESS,
  PASSWORD_UPDATING_ERROR,
  LOGOUT_REQUEST,
  LOGOUT_SUCCESS,
  LOGOUT_ERROR,
  GET_USER_INFO_REQUEST,
  GET_USER_INFO_SUCCESS,
  GET_USER_INFO_ERROR,
  UPDATE_USER_INFO_REQUEST,
  UPDATE_USER_INFO_SUCCESS,
  UPDATE_USER_INFO_ERROR,
  REFRESH_TOKEN_REQUEST,
  REFRESH_TOKEN_SUCCESS,
  REFRESH_TOKEN_ERROR,
} from "../constants/auth";

type TUserState = {
  isAuth: boolean;
  email: string;
  name: string;
  accessToken: string;
  accessTokenExpired: boolean;
  requestToken: string;

  isRegistrationInProcess: boolean;
  isRegistrationSucess: boolean;
  isRegistrationFailed: boolean;

  isAuthorizationInProcess: boolean;
  isAuthorizationSucess: boolean;
  isAuthorizationFailed: boolean;

  isPasswordRecoveryInProcess: boolean;
  isPasswordRecoverySucess: boolean;
  isPasswordRecoveryFailed: boolean;

  isPasswordUpdatingInProcess: boolean;
  isPasswordUpdatingSucess: boolean;
  isPasswordUpdatingFailed: boolean;

  getUserInfoInProcess: boolean;
  getUserInfoSucess: boolean;
  getUserInfoFailed: boolean;

  updateUserInfoInProcess: boolean;
  updateUserInfoSucess: boolean;
  updateUserInfoFailed: boolean;

  refreshTokenInProcess: boolean;
  refreshTokenSucess: boolean;
  refreshTokenFailed: boolean;
};

export const userState: TUserState = {
  isAuth: false,
  email: '',
  name: '',
  accessToken: '',
  accessTokenExpired: false,
  requestToken: '',

  isRegistrationInProcess: false,
  isRegistrationSucess: false,
  isRegistrationFailed: false,

  isAuthorizationInProcess: false,
  isAuthorizationSucess: false,
  isAuthorizationFailed: false,

  isPasswordRecoveryInProcess: false,
  isPasswordRecoverySucess: false,
  isPasswordRecoveryFailed: false,

  isPasswordUpdatingInProcess: false,
  isPasswordUpdatingSucess: false,
  isPasswordUpdatingFailed: false,

  getUserInfoInProcess: false,
  getUserInfoSucess: false,
  getUserInfoFailed: false,

  updateUserInfoInProcess: false,
  updateUserInfoSucess: false,
  updateUserInfoFailed: false,

  refreshTokenInProcess: false,
  refreshTokenSucess: false,
  refreshTokenFailed: false,
};

export const authReducer = (state = userState, action: TUserActions) => {
  switch (action.type) {
    //---------------------------------------------------------------------------REGISTRATION-----------------------------
    case REGISTRATION_REQUEST: {
      return {
        ...state,
        isRegistrationInProcess: true,
      };
    }
    case REGISTRATION_SUCCESS: {
      setCookie('acessToken', action.payload.accessToken.split('Bearer ')[1]);
      setCookie('refreshToken', action.payload.refreshToken);
      return {
        ...state,
        isAuth: true,
        email: action.payload.user.email,
        name: action.payload.user.name,
        accessToken: action.payload.accessToken.split('Bearer ')[1],
        refreshToken: action.payload.refreshToken,
        isRegistrationInProcess: false,
        isRegistrationSucess: true,
      };
    }
    case REGISTRATION_ERROR: {
      return {
        ...state,
        isAuth: false,
        email: '',
        name: '',
        accessToken: '',
        refreshToken: '',
        isRegistrationFailed: true,
        isRegistrationInProcess: false,
      };
    }
    //---------------------------------------------------------------------------AUTHORIZATION-----------------------------
    case AUTHORIZATION_REQUEST: {
      return {
        ...state,
        isAuthorizationInProcess: true,
      };
    }
    case AUTHORIZATION_SUCCESS: {
      setCookie('acessToken', action.payload.accessToken.split('Bearer ')[1]);
      setCookie('refreshToken', action.payload.refreshToken);
      return {
        ...state,
        isAuth: true,
        email: action.payload.user.email,
        name: action.payload.user.name,
        accessToken: action.payload.accessToken.split('Bearer ')[1],
        refreshToken: action.payload.refreshToken,
        isAuthorizationInProcess: false,
        isAuthorizationSucess: true,
      };
    }
    case AUTHORIZATION_ERROR: {
      return {
        ...state,
        isAuth: false,
        email: '',
        name: '',
        accessToken: '',
        refreshToken: '',
        isAuthorizationInProcess: false,
        isAuthorizationSucess: false,
        isAuthorizationFailed: true,
      };
    }
    //---------------------------------------------------------------------------PASSWORD_RECOVERY-----------------------------
    case PASSWORD_RECOVERY_REQUEST: {
      return {
        ...state,
        isPasswordRecoveryInProcess: true,
      };
    }
    case PASSWORD_RECOVERY_SUCCESS: {
      return {
        ...state,
        isPasswordRecoverySucess: true,
        isPasswordRecoveryInProcess: false,
        isPasswordRecoveryFailed: false,
      };
    }
    case PASSWORD_RECOVERY_ERROR: {
      return {
        ...state,
        isPasswordRecoverySucess: false,
        isPasswordRecoveryInProcess: false,
        isPasswordRecoveryFailed: true,
      };
    }
    //---------------------------------------------------------------------------PASSWORD_UPDATING-----------------------------
    case PASSWORD_UPDATING_REQUEST: {
      return {
        ...state,
        isPasswordUpdatingInProcess: true,
      };
    }
    case PASSWORD_UPDATING_SUCCESS: {
      return {
        ...state,
        isPasswordUpdatingSucess: true,
        isPasswordUpdatingInProcess: false,
        isPasswordUpdatingFailed: false,
      };
    }
    case PASSWORD_UPDATING_ERROR: {
      return {
        ...state,
        isPasswordUpdatingSucess: false,
        isPasswordUpdatingInProcess: false,
        isPasswordUpdatingFailed: true,
      };
    }
    //---------------------------------------------------------------------------LOGOUT-----------------------------
    case LOGOUT_REQUEST: {
      return {
        ...state,
        isLogoutInProcess: true,
      };
    }
    case LOGOUT_SUCCESS: {
      deleteCookie('acessToken');
      deleteCookie('refreshToken');
      return {
        ...userState,
        isAuth: false,
        isLogoutInProcess: false,
        isLogoutFailed: false,
        isLogoutSucess: true,
      };
    }
    case LOGOUT_ERROR: {
      return {
        ...state,
        isLogoutInProcess: false,
        isLogoutFailed: true,
        isLogoutSucess: false,
      };
    }
    //---------------------------------------------------------------------------GET_USER_INFO-----------------------------
    case GET_USER_INFO_REQUEST: {
      return {
        ...state,
        getUserInfoInProcess: true,
      };
    }
    case GET_USER_INFO_SUCCESS: {
      return {
        ...state,
        isAuth: true,
        getUserInfoInProcess: false,
        getUserInfoFailed: false,
        getUserInfoSucess: true,
        email: action.payload.user.email,
        name: action.payload.user.name,
      };
    }
    case GET_USER_INFO_ERROR: {
      return {
        ...state,
        isAuth: false,
        getUserInfoSucess: false,
        getUserInfoInProcess: false,
        getUserInfoFailed: true,
        accessTokenExpired: true,
      };
    }
    //---------------------------------------------------------------------------UPDATE_USER_INFO-----------------------------
    case UPDATE_USER_INFO_REQUEST: {
      return {
        ...state,
        updateUserInfoInProcess: true,
      };
    }
    case UPDATE_USER_INFO_SUCCESS: {
      return {
        ...state,
        isAuth: true,
        updateUserInfoInProcess: false,
        updateUserInfoSucess: true,
        updateUserInfoFailed: false,
        email: action.payload.user.email,
        name: action.payload.user.name,
      };
    }
    case UPDATE_USER_INFO_ERROR: {
      return {
        ...state,
        isAuth: false,
        accessTokenExpired: true,
        updateUserInfoInProcess: false,
        updateUserInfoFailed: true,
        updateUserInfoSucess: false,
      };
    }

    //---------------------------------------------------------------------------UPDATE_TOKEN-----------------------------
    case REFRESH_TOKEN_REQUEST: {
      return {
        ...state,
        refreshTokenInProcess: true,
      };
    }
    case REFRESH_TOKEN_SUCCESS: {
      setCookie('acessToken', action.payload.accessToken.split('Bearer ')[1]);
      setCookie('refreshToken', action.payload.refreshToken);
      return {
        ...state,
        refreshTokenInProcess: false,
        refreshTokenSucess: true,
        refreshTokenFailed: false,
      };
    }
    case REFRESH_TOKEN_ERROR: {
      return {
        ...state,
        refreshTokenInProcess: false,
        refreshTokenSucess: false,
        refreshTokenFailed: true,
      };
    }
    default:
      return state;
  }
};