import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "../../services/hooks";
import { wsInit, wsClose } from "../../services/actions/wsActionTypes";
import { NavLink } from "react-router-dom";
import {
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";

import { Switch, Route, useRouteMatch } from "react-router-dom";

import {
  logout,
  updateUserInfo,
  refreshToken,
} from "../../services/actions/auth";
import profileStyles from "./profile.module.css";

import OrderContainer from "../../components/order-container/order-container";
function Profile() {
  const {
    storeUserEmail,
    storeUserName,
    updateUserInfoFailed,
    refreshTokenSucess,
  } = useSelector((store) => ({
    storeUserEmail: store.authData.email,
    storeUserName: store.authData.name,
    updateUserInfoFailed: store.authData.updateUserInfoFailed,
    refreshTokenSucess: store.authData.refreshTokenSucess,
  }));
  const [userEmail, setUserEmail] = useState(storeUserEmail);
  const [userName, setUserName] = useState(storeUserName);
  const [password, setPassword] = useState("");

  const dispatch = useDispatch();

  const handleSubmit = (event: React.SyntheticEvent) => {
    event.preventDefault();
    dispatch(
      updateUserInfo({
        name: userName,
        email: userEmail,
      })
    );
  };

  useEffect(() => {
    dispatch(wsInit(`profile`));

    if (updateUserInfoFailed) {
      dispatch(refreshToken(1));
    }
    return () => {
      dispatch(wsClose());
    };
  }, [updateUserInfoFailed, dispatch]);

  useEffect(() => {
    if (refreshTokenSucess) {
      dispatch(
        updateUserInfo({
          name: userName,
          email: userEmail,
        })
      );
    }
  }, [refreshTokenSucess, userName, userEmail, dispatch]);

  const handleCancel = () => {
    setUserEmail(storeUserEmail);
    setUserName(storeUserName);
  };

  const match = useRouteMatch();
  return (
    <div className={profileStyles.header + " mt-25"}>
      <div className={profileStyles.header__container}>
        <div className={profileStyles.navBar + " pl-5 mr-5"}>
          <div className={profileStyles.navElement}>
            <NavLink
              exact
              to={`/profile`}
              className={
                profileStyles.navElement + " text text_type_main-medium"
              }
              activeClassName={
                profileStyles.activeNavElement + " text text_type_main-medium"
              }
            >
              
            </NavLink>
          </div>
          <div className={profileStyles.navElement}>
            <NavLink
              exact
              to={`/profile/orders`}
              //to="/profile/orders"
              className={
                profileStyles.navElement + " text text_type_main-medium"
              }
              activeClassName={
                profileStyles.activeNavElement + " text text_type_main-medium"
              }
            >
               
            </NavLink>
          </div>
          <div className={profileStyles.navElement}>
            <NavLink
              to="/profile"
              className={
                profileStyles.navElement + " text text_type_main-medium"
              }
              onClick={() => dispatch(logout())}
            >
              
            </NavLink>
          </div>

          <p className="text text_type_main-default text_color_inactive mt-20">
                 <br></br>    
          </p>
        </div>

        <Switch>
          <Route exact path="/profile/orders">
            <div className={profileStyles.orderContainer}>
              <OrderContainer />
            </div>
          </Route>

          <Route path={match.path}>
            <form onSubmit={handleSubmit}>
              <div className="ml-30">
                <Input
                  type={"text"}
                  placeholder={""}
                  onChange={(e) => setUserName(e.target.value)}
                  icon={"EditIcon"}
                  value={userName}
                  name={"name"}
                  error={false}
                  //ref={inputRef}
                  //onIconClick={onIconClick}
                  errorText={""}
                  size={"default"}
                />
                <div className="mt-6">
                  <Input
                    type={"text"}
                    placeholder={"E-mail"}
                    onChange={(e) => setUserEmail(e.target.value)}
                    icon={"EditIcon"}
                    value={userEmail}
                    name={"name"}
                    error={false}
                    //ref={inputRef}
                    //onIconClick={onIconClick}
                    errorText={""}
                    size={"default"}
                  />
                </div>
                <div className="mt-6">
                  <Input
                    type={"text"}
                    placeholder={""}
                    onChange={(e) => setPassword(e.target.value)}
                    icon={"CloseIcon"}
                    value={password}
                    name={"name"}
                    error={false}
                    //ref={inputRef}
                    //onIconClick={onIconClick}
                    errorText={""}
                    size={"default"}
                  />
                </div>
                {userEmail !== storeUserEmail || userName !== storeUserName ? (
                  <div className={profileStyles.profileButtons + " mt-6"}>
                    <p
                      className="text text_type_main-default text_color_inactive mr-7"
                      onClick={handleCancel}
                    >
                      
                    </p>
                    <Button type="primary" size="medium">
                      
                    </Button>
                  </div>
                ) : (
                  ""
                )}
              </div>
            </form>
          </Route>
        </Switch>
      </div>
    </div>
  );
}

export default Profile;
